import { envProvider } from '@engine/core';
import { AbstractLink, HyperLinkProps } from './abstract.link';

export class DownloadLink extends AbstractLink {
    protected id: string;
    public constructor(props: DownloadProps) {
        super(props);
        const { id } = props;
        this.id = id;
    }

    protected _excute() {
        window.open(`${envProvider.FineServletURL}/view/report?op=fr_attach&cmd=ah_download&id=${this.id}`);
    }
}

export interface DownloadProps extends HyperLinkProps {
    id: string;
}
